import React from 'react';
import './Country.css'

const Country = (props) => {
    const{name,flag,capital,population,region}= props.country;
    console.log(props.country);
    return (
        <div className='country' >
            <h2>This is:{name}</h2>
            <img src={flag} alt="" />
            <h4>capital:{capital}</h4>
            <h4>population:{population}</h4>
            <h4>region:{region}</h4>
        </div>
    );
};
